    <!--    </base-material-card>-->

    <base-material-card
      class="px-5 py-3"
      color="indigo"
      icon="mdi-vuetify"
      inline
    >
      <v-row>
        <v-col
          cols="12"
          md="6"
          sm="6"
          class="mt-4"
        >
          <v-date-picker
            v-model="dates"
            range
            full-width
            :max="todayDate"
            locale="de"
          />
        </v-col>
        <v-col
          cols="12"
          md="6"
          sm="6"
        >
          <v-text-field
            v-model="filterDates"
            class="ml-8"
            hide-details
            :label="$t('日期筛选')"
            prepend-icon="mdi-calendar"
            style="max-width: 350px;"
            append-icon="mdi-close"
            @click:append="clear"
          />
          <div style="max-width: 350px">
            <v-btn
              class="ml-8 mt-8 primary"
              style="font-size: large; width: 100%"
              :href="$store.getters.rootCSVUrl"
              @click="exportCSV"
            >
              {{ $t('请点击按钮导出CSV文件！') }}
            </v-btn>
          </div>
        </v-col>
      </v-row>
    </base-material-card>
    <a ref="downloadfile"/>
  </v-container>
</template>

<script>
  import { formatDate } from '@/common/common'
  import moment from 'moment'
  import { exportCSVFromTo } from '@/components/Utlis/api'

  export default {
    props: {
      id: {
        default: -1,
        type: Number,
      },
    },
    data: function () {
      return {
        dates: [],
        menu: false,
      }
    },
    computed: {
      todayDate: function () {
        return moment(new Date()).add(-1, 'days').format('YYYY-MM-DD')
      },
      filterDates: function () {
        if (Date.parse(this.dates[0]) > Date.parse(this.dates[1])) {
          [this.dates[0], this.dates[1]] = [this.dates[1], this.dates[0]]
        }
        console.log('[this.dates[0], this.dates[1]]', '[', this.dates[0] + ' 04:00:00', this.dates[1] + ' 04:00:00')
        return [this.dates[0], this.dates[1]]
      },
    },

    mounted () {
      this.dates = [formatDate(-1), formatDate(-1)]
    },
    methods: {
      async exportCSV () {
        const _data = {
          // startTime: this.filterDates[0],
          // endTime: this.filterDates[1],

          startTime: this.dates[0] + ' 04:00:00',
          endTime: this.dates[1] + ' 04:00:00',
        }
        this.data = await exportCSVFromTo(_data)
        console.log(this.data)
        const content = new Blob([this.data], { type: 'application/zip' })
        const urlObj = window.URL
        const url = urlObj.createObjectURL(content)
        this.$refs.downloadfile.href = url
        this.$refs.downloadfile.acceptCharset = 'UTF-8'
        this.$refs.downloadfile.click()
        urlObj.revokeObjectURL(url)
      },

      clear () {
        this.dates = []
      },

      goBack () {
        this.$router.history.go(-1)
        // this.$router.push({ name: '桌子' })
      },
    },
  }
</script>

